<template>
  <div class="incharge-page">
    <van-nav-bar
      fixed
      border
      title="积分明细"
      left-arrow
      @click-left="goBack"
    />
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="loadMore"
      >
        <van-cell v-for="item in list" :key="item.id">
          <div slot="title">
            <van-col span="12">{{ getTypeName(item.type,item) }}</van-col>
            <van-col
              span="12"
              style="display: flex; justify-content: flex-end;"
              >{{ item.n_integral }}</van-col
            >
          </div>
          <div slot="label">
            <van-col span="12">{{
              moment.unix(item.add_time).format("YY-MM-DD HH-mm")
            }}</van-col>
            <van-col span="12" style="display: flex; justify-content: flex-end;"
              >积分余额：{{
                parseInt(item.p_integral) + parseInt(item.n_integral)
              }}</van-col
            >
          </div>
        </van-cell>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import moment from "moment";
import { mapGetters, mapState } from "vuex";
import { Button, Row, Col, NavBar, List, Cell, PullRefresh } from "vant";
import { getScoreRecords } from "../api/base";

export default {
  name: "scores",
  components: {
    [Row.name]: Row,
    [Col.name]: Col,
    [List.name]: List,
    [Cell.name]: Cell,
    [PullRefresh.name]: PullRefresh,
    [NavBar.name]: NavBar,
    [Button.name]: Button
  },
  data() {
    return {
      moment,
      list: [],
      page: 0,
      limit: 20,
      loading: false,
      finished: false,
      refreshing: false
    };
  },
  computed: {
    ...mapState(["from"]),
    ...mapGetters(["userInfo"])
  },
  methods: {
    // 点击返回按钮
    goBack() {
      if (this.from) {
        this.$router.back();
      } else {
        this.$router.replace("/demands");
      }
    },
    async onRefresh() {
      this.page = 1;
      const list = await this.onLoad();
      this.list = list;
      this.refreshing = false;
    },
    async loadMore() {
      this.page = this.page + 1;
      const list = await this.onLoad();
      this.list = [...this.list, ...list];
      this.loading = false;
    },
    async onLoad() {
      // 异步更新数据
      const res = await getScoreRecords({
        user_id: this.userInfo.id,
        page: this.page,
        limit: this.limit
      });
      this.finished = res.length < this.limit;
      return res;
    },
    getTypeName(type,item) {
      switch (type) {
        case "0":
          return "积分申请";
        case "1":
          return "定向申请";
        case "2":
          return "需求申请";
        case "3":
          return "用户注册";
        case "4":
          return "交换名片";
        case "5":
          return "个人认证通过积分";
		case "7":
		return item.title;
        default:
          return "未知操作";
      }
    }
  }
};
</script>

<style lang="less" scoped>
.score-box {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #c11823;
  color: #fff;

  .score {
    font-size: 32px;
  }
}

.cell-group {
  margin-top: 12px;
}

.intro {
  padding: 10px 15px;
  background-color: #fff;

  h2 {
    font-size: 18px;
    color: #000;
  }

  p {
    font-size: 12px;
    color: #666666;
    margin-bottom: 1em;
  }
}

@media (min-width: 750px) {
  .score-box {
    height: 150px;
    .score {
      font-size: 32px;
    }
  }

  .cell-group {
    margin-top: 12px;
  }

  .intro {
    padding: 10px 15px;

    h2 {
      font-size: 18px;
    }

    p {
      font-size: 12px;
    }
  }
}
</style>
